.barChart__dark .tick line {
  stroke: white; }

.barChart__dark .tick text {
  fill: white; }

.barChartHorizontal__dark .tick line {
  stroke: white; }

.barChartHorizontal__dark .tick text {
  fill: white; }


.lineChart__dark .tick line {
  stroke: white; }

.lineChart__dark .tick text {
  fill: white; }

.hexbinChart__dark .tick line {
  stroke: white; }

.hexbinChart__dark .tick text {
  fill: white; }

.multiLineChart__dark .tick line {
  stroke: white; }

.multiLineChart__dark .tick text {
