.eventsTimeline__label {
  fill: white;
  font-size: 11px;
  letter-spacing: 1px;
  pointer-events: none;
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.5); }

.eventsTimeline__dark .tick line {
  stroke: white; }

.eventsTimeline__dark .tick text {
  fill: white;
  font-size: 11px;
  letter-spacing: 1px; }

.eventsTimeline__event:hover {
  cursor: pointer; }

.eventsTimeline__tooltip {
  color: black;
  position: absolute;
  pointer-events: none;
  font-size: 0.65rem;
  border: 1px solid black;
  border-radius: 3px;
  padding: 5px;
  background-color: white;
  box-shadow: 0px 0px 3px 3px rgba(0, 0, 0, 0.15); }
